	module.exports = {"data":{"content":{"content":"<p><a data-fancybox=\"gallery\" href=\"https://i.imgur.com/CTfB7K9h.jpg\"><img class=\"lozad d-block mx-auto\" data-src=https://i.imgur.com/CTfB7K9l.jpg title=\"Books\" /></a></p>\n<p>在二十一世紀資訊發達的年代，我們很容易在互聯網上獲取這些書本的內容，而且能夠獲取書籍的最新內容。比如說vczero寫的<a href=\"https://github.com/vczero/react-native-lesson\">React Native入门与实战</a>，作者已經在Github上放上自己的書本內容，以及代碼。雖然我在電腦和手機里已經看過這些書，但當我拿起實體書的時候，那種感覺是非常不同的。</p>\n<p>以下這些書都是很值得去閱讀（開坑）的：</p>\n<ol>\n<li>JavaScript 高級程序設計(第三版)</li>\n</ol>\n<p>我在去年的暑假看過一次這本書，感覺能夠學到很多JavaScript的細節。</p>\n<ol>\n<li><p>CSS揭秘</p></li>\n<li><p>圖解HTTP</p></li>\n</ol>\n<p><a href=\"https://calpa.me/2017/04/08/common-http-status-code/\">常見的14種HTTP狀態碼</a></p>\n<ol>\n<li><p>人月神話</p></li>\n<li><p>Clean Code</p></li>\n<li><p>Code Complete</p></li>\n</ol>\n","title":"前端工程師的入門書單","createdDate":"2017-04-05T22:51+08:00","headerImgur":null,"id":"6ivKxa4Q00iq2gU8IWkS4A","toc":[],"tags":"Web"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"6ivKxa4Q00iq2gU8IWkS4A"}}

